.timeline{
    justify-content: center;
    flex-direction: row-reverse;
}
.timeline-event{
    width: 6rem;
    height: 6rem;
    border: 5px solid red;
    border-radius: 50%;
    position: relative;
    margin-right: 5.5rem;
    margin-top: 16rem;
}
.timeline-event > span{
    position: absolute;
    width: 6rem;
    height: .5rem;
    background-color: blue;
    left: -6rem;
    top: calc(50% - .25rem);
}
.timeline-icon{
    width: 8rem;
    height: 8rem;
    position: absolute;
    top: -14rem;
    left: -1.5rem;
    border-radius: 50%;
    color: white;
    text-align: center;
    font-size: 4rem;
    padding-top: calc(100% - 3rem);
}
.timeline-icon > span{
    width: 3rem;
    height: 3rem;
    background-color: red;
    position: absolute;
    bottom: -1.5rem;
}
.timeline-desc{
    position: absolute;
    top: 10rem;
    left: -3rem;
    width: 10rem;
    padding: 1rem;
    font-size: 1.2rem;
    color: #5F6A6A;
}